<template>
  <div id="app">
    <md-editor
      v-model="content"
      style="height: 500px;"
      :toc-option="tocOption"
    />
    <md-render :value="content" :toc-option="tocOption"></md-render>
  </div>
</template>

<script>
import uslug from 'uslug'
import Editor from '@/components/Editor/index.js'
const { MdEditor, MdRender } = Editor
export default {
  name: 'app',
  data () {
    return {
      content: '',
      tocOption: {
        slugify: uslug
      }
    }
  },
  components: {
    MdEditor,
    MdRender
  },
  methods: {
    // 在编辑器上传图片时使用
    async handleImgAdd (pos, file, vm) {
      vm.loading = true
      try {
        const res = await this.upload(file)
        vm.$img2Url(pos, this.$cfg.getImgPath(res.name))
      } catch (e) {
        console.log('上传失败', e)
      }
      vm.loading = false
    }
  }
}
</script>

